import React from "react";
import "./GifDisplay.css";

// taking one GIF result and displaying it on the page

const GifDisplay = ({ gif, isFavorite, addFavorite, deleteFavorite }) => {
  return (
    <div class="gif-container">
      <h3>{gif.title}</h3>
      <img className="gif" src={gif.url} />
      {!isFavorite && (
        <button className="favorite" onClick={() => addFavorite(gif)}>
          {" "}
          Add Favorite
        </button>
      )}
      {isFavorite && (
        <button className="favorite" onClick={() => deleteFavorite(gif.id)}>
          {" "}
          Remove Favorite
        </button>
      )}
    </div>
  );
};

export default GifDisplay;
