import React, { useState, useEffect } from "react";
import GifDisplay from "../Gif/GifDisplay";
import { setSearch, addFavorite, deleteFavorite } from "../../redux/actions";
import "./SearchPage.css";
import { connect } from "react-redux";

// State - query, limit, rating, gifs (pieces of data user is entering in)
// Props - username??

const SearchPage = (props) => {
  const [query, setQuery] = useState("");
  const [rating, setRating] = useState("g");
  const [limit, setLimit] = useState(25);
  const [error, setError] = useState("");
  const [faveIds, setFaveIds] = useState([]);

  // const faveIds = useMemo(() = > {
  //  return props.favorites.map((gif) => gif.id); }, [props.favorite]);
  // delete const faveIds, delete useEffect
  // put useMemo in import React {}

  useEffect(() => {
    let ids = props.favorites.map((gif) => gif.id);
    setFaveIds(ids);
  }, [props.favorites]);

  async function getGifs(query, rating, limit) {
    const key = "2BkANBTRPYGKHGFxk2I5cfkAYdDoendB";
    const url = `https://api.giphy.com/v1/gifs/search?api_key=${key}&q=${query}&limit=${limit}&offset=0&rating=${rating}&lang=en`;
    try {
      setError("");
      let response = await fetch(url);
      let json = await response.json();
      let resGifs = json.data.map((val) => {
        return { id: val.id, title: val.title, url: val.images.original.url };
      });
      props.setSearch(resGifs);
    } catch (e) {
      setError("Something went wrong. Please try again later!");
      props.setSearch([]);
    }
  }

  return (
    <>
      <h1>Search</h1>
      <div className="search-area">
        <label htmlFor="query">Search</label>
        <input
          type="text"
          id="query"
          value={query}
          className="search"
          placeholder="Search"
          onChange={(e) => setQuery(e.target.value)}
        />
      </div>
      <div className="search-area">
        <label htmlFor="rating">Rating </label>
        <select
          id="rating"
          value={rating}
          onChange={(e) => setRating(e.target.value)}
        >
          <option value="g">G</option>
          <option value="pg">PG</option>
          <option value="pg-13">PG-13</option>
          <option value="r">R</option>
        </select>
      </div>
      <div className="search-area">
        <label htmlFor="limit">Limit</label>
        <select
          id="limit"
          value={limit}
          onChange={(e) => setLimit(e.target.value)}
        >
          <option value="10">10</option>
          <option value="25">25</option>
          <option value="50">50</option>
        </select>
        <button class="button" onClick={() => getGifs(query, rating, limit)}>
          Search
        </button>
        <div class="gif-area">
          {error.length > 0 && <h1>{error}</h1>}
          {error.length === 0 &&
            props.gifs.map((v) => (
              <GifDisplay
                key={v.id}
                gif={v}
                isFavorite={faveIds.includes(v.id)}
                deleteFavorite={props.deleteFavorite}
                addFavorite={props.addFavorite}
              />
            ))}
        </div>
      </div>
    </>
  );
};

// add Favorite
// remove Favorite

// here's the actions i want to access
const mapDispatchToProps = {
  setSearch,
  addFavorite,
  deleteFavorite,
};

// here's the piece of state i want to access
// passing the following props
function mapStateToProps(state) {
  return {
    globalState: state,
    gifs: state.search,
    // i want to access props.favorites
    favorites: state.favorites,
    // username: state.user.username,
  };
}

export default connect(mapStateToProps, mapDispatchToProps)(SearchPage);
